import '../style.scss'
import $ from 'jquery';
import { TweenMax, Power2, TimelineLite } from 'gsap/TweenMax';


/****************************/
/* Prismjs - code hilighter */
/****************************/
import Prism from 'prismjs';
Prism.highlightAll();


TweenMax.fromTo($('.sec_tit'),1,
    {opacity:0, y:10},
    {opacity:1, y:0}
);

/****************************/
/* Select                   */
/****************************/
$('.select_custom').each(function(){
    var _this = $(this);
    var _thisDefault = _this.find('.default');
    var _thisOptions = _this.find('.option');
    var _thisOptItem = _thisOptions.find('li > a');

    var select = $('.select_custom');
    var options = $('.option');
    var defaults = select.find('.default');
    var isOpen = false;

    _thisDefault.on('click', function(){
        if(!isOpen){
            isOpen = true;
            defaults.removeClass('on');
            options.hide();
            $(this).addClass('on').next('ul').show();
        } else {
            isOpen = false;
            $(this).removeClass('on').next('ul').hide();
        }
    });

    _thisOptItem.on('click', function(){
        initSelect();
        var selectedText = $(this).text();
        _thisDefault.find('span').text(selectedText);
    });

    $('html').on('click', function(e){
        if(!$(e.target).parents().hasClass('select_custom')){
            initSelect();
        }
    });

    function initSelect(){
        isOpen = false;
        options.hide();
        defaults.removeClass('on');
    }
});

/****************************/
/* Toggle                   */
/****************************/
$('.toggle').each(function(){
    var _this = $(this);
    var _thisTgTit = _this.find('.tg_tit');
    var tgTit = $('.tg_tit');
    var tgCont = $('.tg_cont');
    var isOpen = false;

    _thisTgTit.on('click', function(){
        if(!isOpen){
            isOpen = true;
            tgTit.removeClass('on');
            tgCont.hide();
            $(this).addClass('on').next('.tg_cont').show();
        } else {
            isOpen = false;
            $(this).removeClass('on').next('.tg_cont').hide();
        }
    });

    $('html').on('click', function(e){
        if(!$(e.target).parents().hasClass('toggle')){
            initToggle();
        }
    });

    function initToggle(){
        isOpen = false;
        tgTit.removeClass('on');
        tgCont.hide();
    }

});


/****************************/
/* Layer                    */
/****************************/
$('button').on('click', function(){
    var _button = $(this);
    var layerId = _button.attr('layerId');
    var layer = $('#'+layerId);
    var layerBox = layer.find('.layer_box');
    var layerDim = layer.find('.dim');

    // scrollBar check
    var body = $('body');
    var outerWidth = $(window).outerWidth();
    var innerWidth = $(window).innerWidth();
    var scrollWidth = outerWidth - innerWidth;
    var isScroll = outerWidth > innerWidth;

    if(layerId != undefined){
        layer.show();
        layerBox.attr('tabindex',0).focus();
        TweenMax.from(layerBox, .3, {scale:.95, y:10});
        TweenMax.from(layerDim, .3, {opacity:0});
        
        body.css({'overflow':'hidden'});
        if(isScroll){
            body.css({'paddingRight':scrollWidth+'px'});
        }
    }

    $('.close, .dim, #btnConfirm').on('click', function(){
        $(this).parents('.layer_wrap').hide();
        _button.focus();

        body.css({'overflow':'auto'});
        if(isScroll){
            body.css({'paddingRight':0});
        }

    });
});

$('.layer_box').each(function(){
    var _this = $(this);
    var _focusable = [];
    var _el_firstFocus = '';
    var _el_lastFocus = '';

    _this.find('*').each(function(i, val) {
        if(val.tagName.match(/^A$|AREA|INPUT|TEXTAREA|SELECT|BUTTON/) && parseInt(val.getAttribute("tabIndex")) !== -1) {
            _focusable.push(val);
        }
        if((val.getAttribute("tabIndex") !== undefined) && (parseInt(val.getAttribute("tabIndex")) >= 0)) {
            _focusable.push(val);
        }
    });

    _el_firstFocus = _focusable[0];
    _el_lastFocus = _focusable[_focusable.length-1];

    $(_el_firstFocus).on({
        'keydown' : function(e){
            if (e.target == this){
                var keyCode = e.keyCode;
                if (keyCode == 9){
                    if (e.shiftKey){
                        $(_el_lastFocus).focus();
                        e.preventDefault();
                    }
                }
            }
        }
    });
    $(_el_lastFocus).on({
        'keydown' : function(e){
            var keyCode = e.keyCode;
            if (keyCode == 9){
                if (!e.shiftKey){
                    $(_el_firstFocus).focus();
                    e.preventDefault();
                }
            }
        }
    });
});


/****************************/
/* Tab                      */
/****************************/
$('.tab_group').each(function(){
    var _this = $(this);
    var tabHead = _this.find('.tab_head a');
    var tabHeadWid = tabHead.innerWidth();
    var tabCont = _this.find('.tab_cont > .tcon');
    var line = _this.find('.line');

    // resize line Width reset
    $(window).on('resize', function(){
        tabHeadWid = tabHead.innerWidth();
        line.css({width:tabHeadWid});
    });

    tabHead.on('click', function(){
        var tabIdx = $(this).parent('li').index();
        tabHead.parent('li').removeClass('on').eq(tabIdx).addClass('on');
        tabCont.removeClass('on').eq(tabIdx).addClass('on');
        TweenMax.from(tabCont.eq(tabIdx), 1, {opacity:0, y:5});
    });

    // Move tab
    if($(this).hasClass('move')){
        line.css({width:tabHeadWid});
        tabHead.on('click', function(){
            var thisA = $(this);
            var thisXpos = thisA.offset().left;
            tabHeadWid = thisA.innerWidth();

            line.css({width:tabHeadWid});
            // line.stop().animate({left:thisXpos - 30});
            TweenMax.to(line,.4,{left:thisXpos - 30});
        });

        // resize xPos reset
        $(window).on('resize', function(){
            var activeA = _this.find('.on');
            var thisXpos = activeA.offset().left;
            tabHeadWid = activeA.innerWidth();
            line.css({width:tabHeadWid+1,left:thisXpos - 30});
        });
    }
});




/****************************/
/* Motion                   */
/****************************/
$('.btn_motion').each(function(){
    $(this).on('click', function(e){
        var motionWrap = $(this);
        var ripple = $(this).find('.effect');
        motionWrap.removeClass('on');
        var x = parseInt(e.pageX - $(this).offset().left) - (ripple.width() / 2);
        var y = parseInt(e.pageY - $(this).offset().top) - (ripple.height() / 2);
        ripple.css({top:y, left:x});
        motionWrap.addClass('on');
    });
});


// micro
$('.micro > a').on('click', function(){
    $(this).parent('.micro').toggleClass('on');
});
setInterval(function(){
    $('.micro > a').each(function(){
        $(this).parent('.micro').toggleClass('on');
    });
},2000);

//micro - music 
